<template>
  <div class="single-blank-config">
    <Form :label-width="120">
      <div class="config"
           v-for="(item, index) in currentValue" :key="index">
        <!-- 别名 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'alias' && cType !== 1">
          <Input v-model="item.configData.configData"></Input>
        </Form-item>
        <!-- 输入类型 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'inputType'">
          <Select filterable="filterable"
                :size="'default'"
                :transfer="false"
                v-model="item.configData.configData">
            <Option v-for="(com, index) in typeList"
                    :key="index"
                    :value="com.id">{{ com.name }}</Option>
          </Select>
          <!-- {{ item.configData }} -->
        </Form-item>
        <!-- 输入提示 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'placeholder'">
          <Input v-model="item.configData.configData"></Input>
          <!-- {{ item.configData }} -->
        </Form-item>
        <!-- 最大长度 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'maxLength'">
          <InputNumber v-model="maxLengthDefaultVal"
                           @on-change="numberChange($event, 'maxLength')"></InputNumber>
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
        <!-- 默认行数 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'rows'">
          <InputNumber v-model="rowsDefaultVal"
                           @on-change="numberChange($event, 'rows')"></InputNumber>
        </Form-item>
        <!-- 默认值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'value'">
          <Input v-model="item.configData.configData"></Input>
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
      </div>
    </Form>
  </div>
</template>

<script>
import Editor from '@/components/editor'

export default {
  name: 'single-blank-config',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  watch: {
    currentValue (newValue, oldValue) {
      this.$emit('input', {
        value: this.currentValue
      })
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000011',
      currentValue: null,
      maxLengthN: null,
      maxLengthDefaultVal: null,
      rowsN: null,
      rowsDefaultVal: null,
      valueN: null,
      valueDefaultVal: null,
      typeList: [
        {
          id: 1,
          name: '普通文本'
        },
        {
          id: 6,
          name: '手机'
        },
        {
          id: 7,
          name: '固话'
        },
        {
          id: 8,
          name: '邮件'
        },
        {
          id: 9,
          name: '密码'
        },
        {
          id: 10,
          name: '网站'
        },
        {
          id: 11,
          name: '身份证'
        },
        {
          id: 12,
          name: '汉字'
        },
        {
          id: 13,
          name: '英文'
        }
      ]
    }
  },
  created () {
    this.currentValue = this.deepCopy(this.configs)
    for (const n in this.currentValue) {
      if (this.currentValue[n].code === 'value') {
        this.valueN = n
        this.valueDefaultVal = Number(this.currentValue[n].configData.configData)
      } else if (this.currentValue[n].code === 'maxLength') {
        this.maxLengthN = n
        this.maxLengthDefaultVal = Number(this.currentValue[n].configData.configData)
      } else if (this.currentValue[n].code === 'rows') {
        this.rowsN = n
        this.rowsDefaultVal = Number(this.currentValue[n].configData.configData)
      }
    }
  },
  methods: {
    numberChange (val, name) {
      this.currentValue[this[name + 'N']].configData.configData = val
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>
